








































































































import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import Stairs from "../components/stairs.vue";
import Problem from "../components/problems.vue";
@Component({
  components: {
    Stairs,
    Problem,
  },
})
export default class Five extends Vue {
  @Prop()
  clickToNext!: (c: number, p: number, url: string, point: number) => void;
  pro = "";
  @Prop()
  isNew!: boolean;
  @Prop()
  leave1!: string;
  @Prop()
  leave2!: string;
  created() {
    this.pro = this.isNew
      ? "https://static2.pivotstudio.cn/2021-h5-questions/problems/new/new-five.png"
      : "https://static2.pivotstudio.cn/2021-h5-questions/problems/old/old-five.png";
  }
}
